<template>
  <div>
    <el-row :gutter="20">
        <el-col :span="12">
            <div class="top">用户状态</div>
            <el-card class="left">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <p>当前用户状态：正常</p>
                        <p>当前登录时间：{{nowLoginTime?nowLoginTime.substring(0,10):''}}</p>
                        <p>{{nowLoginTime?nowLoginTime.substring(11,19):''}}</p>
                    </el-col>
                    <el-col :span="12">
                        <p>用户使用时长：111天</p>
                        <p>上次登录时间：{{LastLoginTime?LastLoginTime.substring(0,10):''}}</p>
                        <p>{{LastLoginTime?LastLoginTime.substring(11,19):''}}</p>
                    </el-col>
                </el-row>
            </el-card>
        </el-col>
        <el-col :span="12">
            <div class="top">用户信息</div>
            <el-card class="right">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <p>用户名：admin</p>
                    </el-col>
                    <el-col :span="12">
                        <p>姓名：管理员账号</p>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col :span="24">
                        <p>身份证号码：140424199707073638</p>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col :span="12">
                        <p>联系电话：18835567639</p>
                    </el-col>
                    <el-col :span="12">
                        <p>所属角色：医院审核人员</p>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col :span="24">
                        <p>所属单位：某医院</p>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col :span="12">
                        <p>是否管理员：不是</p>
                    </el-col>
                    <el-col :span="12">
                        <p>编号：xbznyy</p>
                    </el-col>
                </el-row>
            </el-card>
        </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
    data(){
        return{
            LastLoginTime:'',
            nowLoginTime:''
        }
    },
    methods:{
        getTime(){
            var myDate = new Date();
            //获取当前年
            var year = myDate.getFullYear();
            //获取当前月
            var month = myDate.getMonth() + 1;
            //获取当前日
            var date = myDate.getDate();
            var h = myDate.getHours(); //获取当前小时数(0-23)
            var m = myDate.getMinutes(); //获取当前分钟数(0-59)
            var s = myDate.getSeconds();
            //获取当前时间
            var now = year + '/' + this.conver(month) + "/" + this.conver(date) + " " + this.conver(h) + ':' + this.conver(m) + ":" + this.conver(s);
            this.nowLoginTime = now
        },
        conver(s) {
            return s < 10 ? '0' + s : s;
        }
    },
    mounted(){
        this.LastLoginTime = sessionStorage.getItem("LastLoginTime")
        this.getTime()
    }
};
</script>

<style scoped>
